body {
    font-family: Arial, Helvetica, sans-serif;
    color: #a00;
}

.ds-tb {
    display: table;
}
.ds-tb-cell {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}

.do-slide-parent {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.do-slide-container {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.do-slide-section {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.do-slide-init > :not(:first-child) {
    display: none;
}